.buttons-grid {
  margin: 0 auto 0 auto;
}

.buttons-grid .col-auto {
  padding: 0 10px 0 10px;
}

button {
  width: 33px;
  height: 33px;
  font-size: 1rem;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.5);
  background-color: #f9f9f9;
  box-shadow:
    inset -5px 11px rgba(230, 222, 222, 0.1),
    1px 5px rgba(0, 0, 0, 0.1);
  color: #333;
  line-height: 33px;
  font-family: sans-serif;
  text-align: center;
  cursor: pointer;
  outline: none;
  margin: 10px;
}
